// App.js
import React from 'react';
import './App.css';
import DesktopAds from './Component/JS/Home/DesktopAds';
import Navbar from './Component/JS/Home/Navbar';
import MobAds from './Component/JS/Home/MobAds';
import MianServices from './Component/JS/Services/MainServices';
import Ac from './Component/JS/Services/Ac';
import Cleaning from './Component/JS/Services/Cleaning';
import Electrician from './Component/JS/Services/Electrician';
import Carpenter from './Component/JS/Services/Carpenter';
import Painting from './Component/JS/Services/Painting';
import Appliance from './Component/JS/Services/Appliance';
import SofaAd from './Component/JS/Ad/SofaAd';
import PaintingAd from './Component/JS/Ad/PaintingAd';
import Approintment from './Component/JS/Ad/Approintment';
import LogisticAd from './Component/JS/Ad/LogisticAd';
import Footer from './Component/JS/Footer/Footer';
import Login from './Component/JS/Home/Login';
import Register from './Component/JS/Home/Register';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import MainServices from './Component/JS/Services/MainServices';
import Profile from './Component/JS/Home/Profile';

// Booking Routes 
import Booking from './Component/JS/Home/Booking/Booking.js';
import BillingDetails from './Component/JS/Home/Booking/BillingDetails.js';

function App() {
  return (
    <Router>
      <Navbar />
      <Routes>
        <Route path='/Register' element={<Register />} />
        <Route path='/login' element={<Login />} />
        <Route path='/HomeXpress.com' element={
          <>
            <DesktopAds />
            <MobAds />
            <MianServices />
            <Ac />
            <SofaAd />
            <Electrician />
            <Approintment />
            <Appliance />
            <LogisticAd/>
            <Cleaning />
            <MobAds />
            <Carpenter />
            <PaintingAd />
            <Painting />
            <Footer />
          </>
        }/>
        <Route path='/MainServices' element={<> <MainServices/> <Footer /> </>} />
        <Route path='/Appliance' element={<> <Appliance/> <Footer /> </>} />
        <Route path='/Cleaning' element={<> <Cleaning/> <Footer /> </>} />
        <Route path='/Painting' element={<> <Painting/> <Footer /> </>} />
        <Route path='/Electrician' element={<> <Electrician/> <Footer /> </>} />
        <Route path='/Ac' element={<> <Ac/> <Footer /> </>} />
        <Route path='/Carpenter' element={<> <Carpenter/> <Footer /> </>} />
        <Route path='/Profile' element={<Profile/>}/>
        
        {/* Update: Dynamic Booking route */}
        <Route path='/Booking/:serviceId' element={<Booking/>}/>
        <Route path='/BillingDetails' element={<BillingDetails/>}/>
      </Routes>
    </Router>
  );
}

export default App;
