// Booking.js
import React from 'react';
import '../../../CSS/Home/Booking/AcBooking.css';
import { useNavigate, useParams } from 'react-router-dom';

// Yahan ek sample data object diya gaya hai. (Aap apne data source ya API call ke hisaab se update kar sakte hain)
const serviceData = {
  Ac1: {
    img: require('../../../Assets/Img/AcAdImg/1.png'),
    heading: "Servicing & Repairing",
    rating: 5,  
    reviews:20,
    price: "₹1,499",
    discountPrice: "₹5,599",
    discount: "75% off",
    description: "Our AC Servicing & Repairing service ensures your air conditioner performs at its best. Our certified technicians provide thorough cleaning, precise diagnostics, and prompt repairs using genuine parts. Enjoy a cool, energy-efficient home with reliable, hassle-free service that keeps you comfortable all year round."
  },
  Ac2: {
    img: require('../../../Assets/Img/AcAdImg/2.png'),
    heading: "Service Lite",
    rating: 5,  
    reviews:20,
    price: "₹699",
    discountPrice: "₹1,999",
    discount: "-65% off",
    description: "Our Service Lite AC offers essential maintenance to keep your unit running smoothly. With a focused checkup and basic filter cleaning, we enhance efficiency and extend the lifespan of your AC at an affordable cost."
  },
  Ac3: {
    img: require('../../../Assets/Img/AcAdImg/3.png'),
    heading: " Power Saver",
    rating: 5,  
    reviews:20,
    price: "₹1,375",
    discountPrice: "₹2,500",
    discount: "-45% off",
    description: "Our Power Saver AC service optimizes your system for energy efficiency. We fine-tune your air conditioner to reduce power consumption while maintaining peak performance—helping you save on utility bills."
  },
  Ac4: {
    img: require('../../../Assets/Img/AcAdImg/4.png'),
    heading: "Cleaning",
    rating: 5,  
    reviews:20,
    price: "₹499",
    discountPrice: "₹999",
    discount: "-50% off",
    description: "Experience a breath of fresh air with our Cleaning AC service. We deep clean your air conditioner to remove dust, allergens, and buildup, ensuring improved cooling efficiency and healthier indoor air quality."
  },  
  Ac5: {
    img: require('../../../Assets/Img/AcAdImg/5.png'),
    heading: "Repair & Gas Refill",
    rating: 5,  
    reviews:20,
    price: "₹942",
    discountPrice: "₹1,450",
    discount: "-35% off",
    description: "Our Repair & Gas Refill AC service quickly diagnoses and fixes issues while providing precise gas refills. Trust our certified technicians to restore optimal cooling performance and reliability."
  },
  Ac6: {
    img: require('../../../Assets/Img/AcAdImg/6.png'),
    heading: "Install & Uninstall",
    rating: 5,  
    reviews:20,
    price: "₹361",
    discountPrice: "₹669",
    discount: "-46% off",
    description: "Enjoy hassle-free AC management with our Install & Uninstall AC service. Our experts ensure safe installation and removal of your air conditioning units, delivering a seamless and efficient experience."
  }
  // Agar aapke paas aur services hain, toh unko yahan add kar sakte hain.
};

export default function Booking() {
  const navigate = useNavigate();
  const { serviceId } = useParams(); // Dynamic parameter from URL

  // Agar serviceId match nahi hota, toh default "ac" service data use karein
  const service = serviceData[serviceId] || serviceData["ac"];

  return (
    <div id='Booking-Container'>
      <div id='Main-Booking'>
        <div className='Booking-left'>
          <div className='Booking-Img'>
            <img src={service.img} alt={service.heading} />
          </div>
          <div className='Booking-Button'>
            <button className='Book-now' onClick={() => navigate("/BillingDetails", { state: { price: service.price } })}>
              <i className="ri-heart-fill"></i> Book now
            </button>
          </div>
        </div>

        <div className='Booking-Info'>
          <div className='Booking-heading'>
            <h1>{service.heading}</h1>
          </div>

          <div className='Booking-Rating'>
            <div>
              {Array.from({ length: service.rating }).map((_, index) => (
                <span key={index}><i className="ri-star-s-fill"></i></span>
              ))}
            </div>
            <div className='booking-review'>
              <p>({service.reviews} reviews)</p>
            </div>
          </div>

          <div className='booking-price'>
            <span className='booking-main-price'>{service.price}</span>
            <span className='booking-discount-price'><strike>{service.discountPrice}</strike></span>
            <span className='booking-discount'>{service.discount}</span>
          </div>

          <div className='Booking-Description'>
            <p>{service.description}</p>
          </div>
        </div>
      </div>
    </div>

  );
}
